.font-s10 {
  font-size: 10px !important;
}

.bg-gradient {
  background: linear-gradient(90deg, #6ad6c0, #6292c6);
}

.icon-img {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

  img {
    width: 100%;
    max-width: 80px;
  }

  .m-size {
    max-width: 52px;
  }

  @include bp-md-down {
    .m-size {
      max-width: 52px;
      margin-top: 14px;
    }
  }
}

.font-w100 {
  font-weight: 100;
  display: inline;
}

#back-to-top {
  display: none;
}

.intro {
  padding: 70px 0;
  padding-bottom: 120px;
}

.angle--top:before {
  margin-top: -140px;
}

.angle:before {
  transform: skewY(-2deg);
  min-height: 200px;
}

.section-testimonials {
  .btn-slide {
    max-width: 230px;
    margin: 90px auto 20px auto;
  }
}

@media only screen and (max-width: 991px) {
  .angle--primary-gd:before {
    background: linear-gradient(90deg, #6ad6c0, #6292c6);
  }
}

.font-NSR {
  font-family: "NanumSquareRound", serif !important;
}

@include bp-md-down {
  .scroll-down {
  }

  .btn-appdown {
    position: fixed;
    right: -10px;
    bottom: 0;
    z-index: 100;
    padding: 0;
    max-width: 220px;
    margin: 0;
  }
}
.tp-loader.spinner4 .dot1,
.tp-loader.spinner4 .dot2 {
  background-color: #6ad6c0 !important;
  box-shadow: none;
}

#home {
  height: 100vh;
  background-image: url("../img/hero/hero_1-min.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.video-wrap {
  position: relative;
  display: flex;
  align-items: center;

  &::before {
    display: table;
    width: 100%;
    height: 100%;
    content: "";
    background-color: rgba(0, 0, 0, 0.25);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 1;
  }

  .video-text {
    position: absolute;
    left: 20px;
    color: #fff;
    z-index: 2;
    font-size: 2rem;
    font-weight: 600;
    letter-spacing: 0.025px;
    text-shadow: 0 0 30px rgba(0, 0, 0, 0.25);
  }

  @include bp-sm-up {
    .video-text {
      left: 10%;
      font-size: 3rem;
    }
  }
  @include bp-md-up {
    .video-text {
      left: 10%;
      font-size: 4rem;
    }
  }
  @include bp-lg-up {
    .video-text {
      left: 5%;
      font-size: 5.5rem;
    }
  }
}

@media (max-height: 600px) {
  #home .scroll-down {
    display: none;
  }
}

.video-top {
  .video-wrap {
    width: 100vw;
    height: 60vh;
    video {
      width: 100%;
      height: 60vh;
      object-fit: cover;
      display: block;
    }
  }

  @include bp-md-up {
    .video-wrap {
      width: 100vw;
      height: 100vh;

      video {
        width: 100%;
        height: 100vh;
      }
    }
  }
}

.video-btm {
  padding: 20px;
  .video-wrap {
    video {
    }
  }

  @include bp-md-up {
    padding: 50px;
    .video-wrap {
      video {
      }
    }
  }
}

.slider-padding {
  padding: 50px;
}

.doc-wrap {
  .mb-10 {
    margin-bottom: 10px;
  }
  .row {
    margin-bottom: 20px;
  }
  p {
    margin-bottom: 10px;
    line-height: 1.5;
    font-size: 14px;
  }
  .doc-list {
    padding-left: 20px;
    li {
      margin-bottom: 10px;
      line-height: 1.5;
      font-size: 14px;
    }
  }
}

.m-only {
  display: none;
}

@include bp-xl-down {
}

@include bp-lg-down {
}

@include bp-md-down {
  .m-none {
    display: none;
  }
  .m-main-title {
    font-size: 64px;
    line-height: 1.25;
    text-shadow: 0 0 20px rgba(0, 0, 0, 0.45);
  }

  .intro {
    padding-top: 0;
    padding-bottom: 70px;
  }
  .angle:before {
    transform: skewY(-3deg);
    min-height: 100px;
  }
  .angle--top:before {
    margin-top: -70px;
  }
  .section-title {
    font-size: 2rem;
  }

  .m-only {
    display: block;
  }

  .slider-padding {
    padding: 20px;
  }
  .service__title {
    font-size: 1.5rem;
  }
  .service__text {
    font-size: 1rem;
  }

  #back-to-top {
    z-index: 90;
    display: block;
    left: 20px;
    opacity: 0;

    &.show {
      z-index: 110;
      bottom: 30px;
      opacity: 1;
    }
  }

  .py-md-0 {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
}

