/*-------------------------------------------------------*/
/* Service Boxes
/*-------------------------------------------------------*/
.service {
  margin-bottom: 70px;

  &__icon {
    font-size: 4rem;
    line-height: 1;
    color: $main-color;
    display: inline-block;
    margin-bottom: 32px;
  }

  &__title {
    font-size: 2.25rem;
    font-weight: 800;
    font-family: "Raleway";
    margin-bottom: 18px;
    letter-spacing: 0.25px;
  }

  &__text {
    margin-bottom: 0;
    color: $text-color;
    font-size: 1.25rem;
  }

  a {
    color: $text-color;

    &:hover {
      color: $main-color;
    }
  }
}
