/*-------------------------------------------------------*/
/* Footer
/*-------------------------------------------------------*/
.footer {
  @include bp-lg-up {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
  }
}

/* Footer Widgets
-------------------------------------------------------*/
.footer__widgets {
  padding: 92px 0 48px;

  .social,
  a:hover,
  a:focus {
    color: #fff;
  }

  @include bp-lg-up {
    .row > div:last-child {
      text-align: right;
    }
  }
}

/* Bottom Footer
-------------------------------------------------------*/
.copyright {
  display: inline-block;
  color: $text-color;
  font-size: 0.875rem;

  &-wrap {
    padding: 40px 0;
    border-top: 1px solid $border-color-dark;
  }

  a {
    color: inherit;

    &:hover,
    &:focus {
      color: $main-color;
    }
  }
}

.footer {
  .footer-top {
    margin-bottom: 16px;
  }
  .footer__widgets {
    padding: 60px 0 30px;
  }

  .widget-about-us {
    margin-top: 6px;
    margin-bottom: 0;
  }

  .logo {
    max-height: 30px;
  }
  .footer-info {
    margin-top: 10px;
  }
  .footer-info li {
    color: #686868 !important;
    font-size: 10px;
    margin-bottom: 6px;
  }

  .footer-right {
    display: flex;
    flex-flow: column;
  }

  .copyright-wrap {
    margin-top: auto;
    padding: 0;
    border-top: 0;
  }

  .widget-address {
    margin-bottom: 20px;
  }

  .footer-social {
    text-align: right;
    align-self: flex-end;
  }

  .social {
    font-size: 1.75rem;
  }
}

@include bp-xl-down {
  .footer {
    .footer-top {
      margin-bottom: 0px;
    }
    .footer-social {
      margin-top: 12px;
    }
    .footer-social,
    .footer-policy {
      text-align: left;
    }
  }
}

@media only screen and (max-width: 991px) {
  .footer {
    .logo {
      display: block !important;
    }
  }
}

@include bp-md-down {
  .footer .copyright-wrap {
    text-align: left;
    margin-top: 20px;
  }
}
