/*-------------------------------------------------------*/
/* Projects
/*-------------------------------------------------------*/
.project {
  position: relative;
  overflow: hidden;

  &-slide {
    margin: 0 19px;
  }

  &__img-holder {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }

  &__img {
    width: 100%;
    @include transition($all);
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }

  &__title,
  &__category {
    opacity: 0;
    @include transition(opacity 0.2s cubic-bezier(0.07, 0.98, 0, 0.96));
  }

  &__description-wrap {
    position: absolute;
    opacity: 0;
    bottom: 40px;
    left: 40px;
    display: inline-block;
    width: 78%;
    max-width: 360px;
    -webkit-clip-path: inset(0 0 0 100%);
    clip-path: inset(0 0 0 100%);
    @include transition(opacity 0.2s 0.3s, clip-path 0s 0.3s);
  }

  &__description {
    position: relative;
    padding: 16px 20px;

    &:after {
      content: "";
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: $main-color;
      background: $main-gradient-r45;
      -webkit-transform-origin: 0 50%;
      -moz-transform-origin: 0 50%;
      transform-origin: 0 50%;
      @include transform(scaleX(0));
      @include transition(transform 0.35s cubic-bezier(0.07, 0.98, 0, 0.96));
    }
  }

  &:hover {
    .project__description-wrap {
      opacity: 1;
      -webkit-clip-path: inset(0 0 0 0);
      clip-path: inset(0 0 0 0);
      -webkit-transition: -webkit-clip-path 0.8s
        cubic-bezier(0.07, 0.98, 0, 0.96);
      transition: clip-path 0.8s cubic-bezier(0.07, 0.98, 0, 0.96);
    }

    .project__description {
      &:after {
        -webkit-transform-origin: 100% 50%;
        -moz-transform-origin: 100% 50%;
        transform-origin: 100% 50%;
        @include transform(scaleX(1));
        @include transition(transform 0.7s cubic-bezier(0.07, 0.98, 0, 0.96));
      }
    }

    .project__title,
    .project__category {
      opacity: 1;
    }
  }

  &__url {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__title {
    font-size: 1.65rem;
    position: relative;
    z-index: 2;
    color: rgba($color: #ffffff, $alpha: 1);
  }

  &__category {
    position: relative;
    z-index: 2;
    display: inline-block;
    // margin-top: 16px;
    font-size: 1.25rem;
    font-weight: 300;
    color: rgba($color: #ffffff, $alpha: 0.85);
  }
}

/* Filter
-------------------------------------------------------*/
.project-filter {
  list-style: none;
  margin-bottom: 96px;
  cursor: default;
  font-size: 2rem;
  font-weight: 700;

  a {
    display: inline-block;
    position: relative;
    margin-right: 32px;
    color: $heading-color;
    text-decoration: none;
    letter-spacing: -0.05em;

    &:last-child {
      margin: 0;
    }

    &:after {
      content: "";
      display: block;
      position: absolute;
      height: 2px;
      bottom: 2px;
      left: 0;
      width: 0;
      background-color: $main-color;
      @include transition(0.3s ease-in-out);
    }

    &:hover {
      color: $main-color;
    }
  }

  a.active,
  a:hover,
  a:focus {
    &:after {
      width: 100%;
    }
  }
}

/* Grid
-------------------------------------------------------*/
.project-grid--5-col .project {
  width: 20%;
}
.project-grid--4-col .project {
  width: 25%;
}
.project-grid--3-col .project {
  width: 33.33333333%;
}
.project-grid--2-col .project {
  width: 50%;
}

.projects-slider.class-slider {
  .is-selected {
    .project.project-slide {
    }
  }

  .project.project-slide {
    border-radius: 8px;
    margin: 10px 19px;
  }

  .project__description-wrap {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 1;
    display: flex;
    height: 100%;
    width: 100%;
    max-width: 100%;
    -webkit-clip-path: initial;
    clip-path: initial;
    transition: all ease-in-out 0.3s;
    background: rgb(0, 0, 0);
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.05) 0%,
      rgba(0, 0, 0, 0.35) 100%
    );
  }

  .project__description {
    padding: 30px 40px;
    display: flex;
    flex-flow: column;
    flex: 1 1 auto;
  }

  .project__description:after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparent;
    background: transparent;

    transform-origin: 0 50%;
    transform: scaleX(0);
    transition: transform 0.35s cubic-bezier(0.07, 0.98, 0, 0.96);
  }

  .top-name {
    margin-bottom: auto;

    img {
      max-width: 70px;
      margin-right: 10px;
      border-radius: 50%;
    }
    span {
      color: #fff;
      font-size: 1.5rem;
      font-weight: 700;
    }
  }

  .project__category,
  .project__title {
    opacity: 1;
  }

  .project__title {
    font-size: 2.5rem;
  }

  .project__category {
    font-size: 1.5rem;
  }

  .hover-shrink:hover {
    .hover-shrink--shrink {
      transform: scale(1);
    }

    .hover-shrink--zoom {
      transform: scale(1.05);
    }
  }

  @include bp-md-down {
    .top-name {
      img {
        max-width: 50px;
      }
      span {
        font-size: 1.25rem;
      }
    }

    .project__title {
      font-size: 1.85rem;
    }

    .project__category {
      font-size: 1.25rem;
    }
  }
}
