/*-------------------------------------------------------*/
/* Contact Form
/*-------------------------------------------------------*/
.privacy {
  &-modal {
    padding: 0 !important;

    &__dialog {
      display: flex;
      margin: 0;
      max-width: 100%;
    }

    &__content {
      border-radius: 0;
      border: 0;
      justify-content: center;
      flex-direction: row;
      height: 100%;
    }

    &__body {
      flex: 1 0 0;
      max-width: 1000px;
      margin: auto;
      padding: 40px 1rem;
      word-break: keep-all;
      font-size: 14px;

      .sub-title {
        margin-top: 42px;
      }
      .lead {
        margin: 32px 0 16px 0;
      }

      ul {
        margin-bottom: 24px;
        padding-left: 20px;
        li {
          margin-bottom: 12px;
          ul {
            margin-top: 12px;
          }
        }
      }
      .list-decimal {
        li {
          list-style: decimal;
        }
      }
      .list-hangul {
        li {
          list-style: hangul;
        }
      }
      .list-disc {
        li {
          list-style: disc;
        }
      }
      .table {
        margin: 16px 0 32px 0;
        width: 100%;
      }
    }

    &__close {
      width: 40px;
      height: 40px;
      position: fixed;
      top: 40px;
      right: 60px;
      z-index: 10;

      span {
        font-size: 0;

        &:before,
        &:after {
          content: "";
          display: block;
          position: absolute;
          top: 20px;
          height: 2px;
          width: 40px;
          background-color: $bg-dark;
        }

        &:before {
          @include transform(rotate(-45deg));
        }

        &:after {
          @include transform(rotate(45deg));
        }
      }

      &:hover,
      &:focus {
        background-color: transparent;
      }

      @include bp-md-down {
        top: 20px;
        right: 20px;
        background-color: #fff !important;
      }
    }
  }
}

.contact-form__message {
  height: 50px;
  width: 100%;
  font-size: 13px;
  line-height: 50px;
  text-align: center;
  float: none;
  margin-top: 20px;
  display: none;
  color: #fff;

  &.error {
    background-color: #f8d7da;
    color: #721c24;
  }

  &.success {
    background-color: #d4edda;
    color: #155724;
  }
}
