/*-------------------------------------------------------*/
/* Team
/*-------------------------------------------------------*/

.m-scroll {
  display: none;

  img {
    min-width: 50px;
  }
}

@include bp-lg-down {
  .m-scroll {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    font-size: 10px;
    letter-spacing: 1px;

    svg:first-child {
      opacity: 0.2;
      animation: arrow-left 1.5s ease-in-out infinite;
    }

    svg:last-child {
      opacity: 0.2;
      animation: arrow-right 1.5s ease-in-out infinite;
    }
  }

  @keyframes arrow-left {
    0% {
      transform: translateX(0);
    }

    50% {
      transform: translateX(4px);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes arrow-right {
    0% {
      transform: translateX(0) rotate(180deg);
    }

    50% {
      transform: translateX(-4px) rotate(180deg);
    }
    100% {
      transform: translateX(0) rotate(180deg);
    }
  }

  .res-row-line {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: scroll;

    .col-lg-4 {
      min-width: 80%;
    }

    .team__name {
      font-size: 1.25rem;
    }
  }
}

.team {
  margin-bottom: 32px;

  ul {
    list-style: none;
  }

  &:hover {
    .team__img {
      transform: scale(1.05);
    }
    .team__name {
      color: $main-color;
      transform: translateX(10px);
    }
  }

  &__img {
    width: 100%;
    transition: all ease-in-out 0.4s;
  }

  &__name {
    font-size: 1.75rem;
    margin-top: 30px;
    margin-bottom: 15px;
    transition: all ease-in-out 0.2s;
    height: 72px;
    display: flex;
    align-items: center;
  }

  &__position {
    font-size: 0.9375rem;
    text-transform: uppercase;
    display: inline-block;
    margin-bottom: 14px;
  }

  ul {
    transition: all ease-in-out 0.2s;
    transition-delay: 200ms;
  }

  @include bp-lg-down {
    margin-bottom: 56px;
  }

  @include bp-md-down {
    .team__name {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
}
