/*-------------------------------------------------------*/
/* Buttons
/*-------------------------------------------------------*/
.btn {
  font-weight: 700;
  font-family: $body-font;
  overflow: hidden;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  height: auto;
  width: auto;
  border: 0;
  border-radius: 0;
  letter-spacing: -0.05em;
  @include transition(all 0.2s ease-in-out);
  color: #fff;
  background-color: $main-color;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:hover {
    color: #fff;
    // box-shadow: 0 12px 20px rgba($main-color, .25);
    background-color: $secondary-color;
    border-color: transparent;
  }

  &:focus {
    outline: none;
    color: #fff;
  }

  &--lg {
    font-size: 1.125rem;
    padding: 15px 40px;
  }

  &--md {
    font-size: 0.9375rem;
    padding: 12px 32px;
  }

  &--color {
    background-color: $main-color;

    &:hover {
      opacity: 0.92;
    }
  }

  &--stroke {
    border: 2px solid $main-color;
    background-color: transparent;
    color: $main-color;
  }

  &--dark {
    background-color: $bg-dark;
  }

  &--white {
    background-color: #fff;
    color: $heading-color;
  }

  &--light {
    background-color: $bg-light;
    color: $heading-color;
  }

  &--wide {
    width: 100%;
  }

  &--stroke:hover,
  &--stroke:focus,
  &--dark:hover,
  &--dark:focus,
  &--white:hover,
  &--white:focus,
  &--light:hover,
  &--light:focus {
    background-color: $main-color;
    border-color: transparent;
  }

  i {
    font-size: 0.75rem;
    position: relative;
    margin-left: 14px;
    line-height: 1;
  }
}

.rounded,
.rounded:before {
  border-radius: 70px;
}

.section-buttons {
  margin-bottom: 50px;

  a {
    margin-bottom: 10px;
  }
}

/* Input Buttons
-------------------------------------------------------*/
.btn--button {
  border: none;
  margin-bottom: 0;
  width: auto;

  &.btn--color,
  &.btn--dark {
    color: #fff;

    &:focus {
      background-color: $main-color;
    }
  }

  &.btn--wide {
    width: 100%;
  }
}

.btn-slide {
  background: #000;
  border: 2px solid #fff;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 26px 15px 26px;
  border-radius: 70px;
  font-size: 1.35rem;
  font-weight: 800;
  line-height: 1;
  font-family: NanumSquareRound;
  letter-spacing: 0.25px;

  &:hover {
    background: $main-color;
  }

  &.btn-slide-btm {
    min-width: 230px;
    margin: 6px 10px;

    &:last-child {
      background: $meta-color;
      cursor: default;
    }
  }
}

.btn-icon {
  background-size: 24px;
  background-position: 50%;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-right: 12px;
  background-repeat: no-repeat;
}

.btn-play {
  background-image: url(../img/button/btn-play.png);
}

.btn-ios {
  background-image: url(../img/button/btn-ios.png);
}

.btn-appdown {
  position: fixed;
  right: 0px;
  bottom: 0px;
  z-index: 100;
  padding: 0;
  max-width: 300px;
  margin: 0;

  img {
    width: 100%;
  }
}
