/*-------------------------------------------------------*/
/* Sliders
/*-------------------------------------------------------*/

/*! Flickity v2.2.1
https://flickity.metafizzy.co
---------------------------------------------- */
.flickity-enabled {
  position: relative;
}
.flickity-enabled:focus {
  outline: 0;
}
.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}
.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}
.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.flickity-button {
  position: absolute;
  background: hsla(0, 0%, 100%, 0.75);
  border: none;
  color: #333;
}
.flickity-button:hover {
  background: #fff;
  cursor: pointer;
}
.flickity-button:focus {
  outline: 0;
  box-shadow: 0 0 0 5px #19f;
}
.flickity-button:active {
  opacity: 0.6;
}
.flickity-button:disabled {
  opacity: 0.3;
  cursor: auto;
  pointer-events: none;
}
.flickity-button-icon {
  fill: currentColor;
}
.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transform: translateY(-50%);
}
.flickity-prev-next-button.previous {
  left: 10px;
}
.flickity-prev-next-button.next {
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}
.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}
.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -25px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}
.flickity-rtl .flickity-page-dots {
  direction: rtl;
}
.flickity-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  background: #333;
  border-radius: 50%;
  opacity: 0.25;
  cursor: pointer;
}
.flickity-page-dots .dot.is-selected {
  opacity: 1;
}

.flickity-slide {
  width: 100%;

  @include bp-lg-up {
    width: 40%;
  }
}

.trainer-slider .flickity-slide {
  width: 80%;

  @include bp-lg-up {
    width: 23%;
  }

  .project {
    border-radius: 10px;
  }
  .hover-shrink:hover .hover-shrink--zoom {
    transform: scale(1);
  }
  .hover-shrink:hover .hover-shrink--shrink {
    transform: scale(1.1);
  }

  .project__description-wrap {
    position: absolute;
    bottom: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    max-width: 100%;
    -webkit-clip-path: inherit;
    clip-path: inherit;
    transition: opacity 0.2s 0.3s, -webkit-clip-path 0s 0.3s;
    transition: opacity 0.2s 0.3s, clip-path 0s 0.3s;
    transition: opacity 0.2s 0.3s, clip-path 0s 0.3s, -webkit-clip-path 0s 0.3s;
    height: 100%;
  }

  .project__description {
    position: relative;
    padding: 16px 20px;
    height: 100%;
    width: 100%;
    z-index: 2;
  }

  .project__description:after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba($color: #000000, $alpha: 0.25);
    background: rgba($color: #000000, $alpha: 0.25);
    transform-origin: 50% 50%;
    transform: scaleX(0);
    transition: transform 0.35s cubic-bezier(0.07, 0.98, 0, 0.96);
  }

  .project__category {
  }

  .project__title {
    position: absolute;
    top: 40px;
    left: 30px;
    height: 100%;
    text-shadow: 4px 4px 18px rgba(0, 0, 0, 0.35);
    transition: all ease-in-out 0.5s;
  }

  .tag {
    list-style: none;
    font-size: 1.25rem;
    font-weight: 300;
    color: hsla(0, 0%, 100%, 0.85);

    li {
      margin-top: 6px;
    }
  }

  .project__category {
    position: absolute;
    left: 20px;
    bottom: 0;
    font-size: 1rem;
    font-weight: 600;
    padding: 30px;
    box-sizing: border-box;
    word-break: keep-all;
    transition: all ease-in-out 0.3s;
    display: none;
  }

  .project__title__wrap {
    position: absolute;
    z-index: 1;
    top: 260px;
    left: 30px;
    color: #fff;
    opacity: 1;
    transition: all ease-in-out 0.5s;

    .view-title {
      text-shadow: 4px 4px 18px rgba(0, 0, 0, 0.35);
      transition: all ease-in-out 0.5s;
      color: #fff;
    }
    .tag {
      text-shadow: 4px 4px 18px rgba(0, 0, 0, 0.35);
    }
  }

  .project:hover {
    .project__description:after {
      transform-origin: 100% 100%;
    }
    .project__title {
      top: 30px;
    }

    .project__category {
      left: 0;
    }

    .project__title__wrap {
      opacity: 0;
      top: 280px;
    }
  }

  @include bp-md-down {
    .m-hover {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      border: 8px solid rgba($color: #ffffff, $alpha: 0.5);
      position: absolute;
      left: 0;
      right: 0;
      bottom: 20px;
      margin: auto;
      transition: all ease-in-out 0.2s;
    }
    .m-hover span {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: rgba($color: #ffffff, $alpha: 0.85);
    }
    .hover-shrink:hover .m-hover {
      opacity: 0;
    }
  }
}

// Dots
.flickity-page-dots .dot {
  background-color: $text-color;

  &.is-selected {
    background-color: $main-color;
  }
}

@media only screen and (min-width: 992px) {
  .projects-slider.class-slider {
    .flickity-slide {
      width: 40%;
    }
  }
}

.rev_slider .tp-mask-wrap .tp-caption,
.rev_slider .tp-mask-wrap :last-child,
.wpb_text_column .rev_slider .tp-mask-wrap .tp-caption,
.wpb_text_column .rev_slider .tp-mask-wrap :last-child {
  margin-bottom: 0;
}
